#root {
  position: fixed;
  inset: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1em;
}

a.blue-button {
  text-decoration: none;
  padding: 0.5em;
  color: #5db1ff;

  border: 0.1em solid #5db1ff;
  border-radius: 0.5em;

  text-align: center;

  &:hover {
    background-color: #c1e0fd;
  }

  &:visited {
    color: #5db1ff;
  }
}
