div.game-modes {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  justify-content: center;

  font-size: 2em;
  padding: 0.5em;

  box-shadow: 0 0 0.5em 0.05em #c1e0fd;
  border-radius: 0.5em;

  // Show "title" attribute as content on mobile
  @media (pointer: coarse) {
    font-size: 1.5em;
    margin: 0 1em;

    a {
      align-items: center;
      display: flex;
      justify-content: center;
      flex-direction: column;
      gap: 0.3em;
      padding: 0.2em 0.5em;

      &::after {
        content: "(" attr(title) ")";
        font-size: 0.5em;
        font-weight: bold;
        text-align: center;
        margin-bottom: 0.5em;
        color: #acacac;
      }
    }
  }
}
