#root {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
  display: flex;
  position: fixed;
  inset: 0;
}

a.blue-button {
  color: #5db1ff;
  text-align: center;
  border: .1em solid #5db1ff;
  border-radius: .5em;
  padding: .5em;
  text-decoration: none;
}

a.blue-button:hover {
  background-color: #c1e0fd;
}

a.blue-button:visited {
  color: #5db1ff;
}

svg.game-grid {
  aspect-ratio: 1;
  pointer-events: none;
}

svg.game-grid > g {
  pointer-events: all;
}

div.game-result {
  background-color: #00000080;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

div.game-result > div.result-container {
  background-color: #fff;
  border-radius: .5em;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: .5em;
  padding: 1em;
  font-size: 2em;
  display: flex;
}

div.game-result > div.result-container > svg.game-grid {
  width: min(10em, 60vw);
}

div.game-container {
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
  display: flex;
  overflow: hidden;
}

div.game-container > svg.game-grid, div.game-container > div.game-grid-container {
  width: 40vw;
  min-width: 20em;
}

div.game-container > div.game-grid-container {
  position: relative;
}

div.game-container > div.game-grid-container > svg.game-grid {
  width: 100%;
}

div.game-container > div.game-grid-container > div.game-grid-overlay {
  pointer-events: none;
  color: #fff;
  -webkit-backdrop-filter: blur(.1em);
  backdrop-filter: blur(.1em);
  background-color: #00000080;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  display: flex;
  position: absolute;
  inset: 0;
}

div.game-container.anonymous-multiplayer {
  position: relative;
}

div.game-container.anonymous-multiplayer > div.loading-overlay {
  -webkit-backdrop-filter: blur(.5em);
  backdrop-filter: blur(.5em);
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

div.game-container.anonymous-multiplayer > div.loading-overlay > div.status-text {
  color: #fff;
  font-size: 2em;
  font-weight: 600;
}





div.game-modes {
  border-radius: .5em;
  flex-direction: column;
  justify-content: center;
  gap: .5em;
  padding: .5em;
  font-size: 2em;
  display: flex;
  box-shadow: 0 0 .5em .05em #c1e0fd;
}

@media (pointer: coarse) {
  div.game-modes {
    margin: 0 1em;
    font-size: 1.5em;
  }

  div.game-modes a {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: .3em;
    padding: .2em .5em;
    display: flex;
  }

  div.game-modes a:after {
    content: "(" attr(title) ")";
    text-align: center;
    color: #acacac;
    margin-bottom: .5em;
    font-size: .5em;
    font-weight: bold;
  }
}

